'use strict';

/**
* LAPACK routine to set the off-diagonal elements and the diagonal elements of a double-precision complex floating-point matrix to specified values.
*
* @module @stdlib/lapack-base-zlaset
*
* @example
* import Complex128Array from '@stdlib/array-complex128';
* import Complex128 from '@stdlib/complex-float64-ctor';
* import zlaset from '@stdlib/lapack-base-zlaset';
*
* var A = new Complex128Array( 4 );
* var alpha = new Complex128( 1.0, 2.0 );
* var beta = new Complex128( 3.0, 4.0 );
*
* zlaset( 'row-major', 'all', 2, 2, alpha, beta, A, 2 );
*
* var z = A.get( 0 );
* // returns <Complex128>[ 3.0, 4.0 ]
*
* @example
* import Complex128Array from '@stdlib/array-complex128';
* import Complex128 from '@stdlib/complex-float64-ctor';
* import zlaset from '@stdlib/lapack-base-zlaset';
*
* var A = new Complex128Array( 5 );
* var alpha = new Complex128( 1.0, 2.0 );
* var beta = new Complex128( 3.0, 4.0 );
*
* zlaset.ndarray( 'all', 2, 2, alpha, beta, A, 2, 1, 1 );
*
* var z = A.get( 1 );
* // returns <Complex128>[ 3.0, 4.0 ]
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var zlaset;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	zlaset = main;
} else {
	zlaset = tmp;
}


// EXPORTS //

export default zlaset;

// exports: { "ndarray": "zlaset.ndarray" }
